<template>
  <el-container class="home-container">
    <el-header>
      <div>
        <span>眼科数据标定</span>
      </div>
    </el-header>
    <el-container>
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <el-menu
          background-color="#c7e3f3"
          text-color="rgb(108, 108, 112)"
          active-text-color="rgb(10, 10, 245)"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="activePath"
        >
          <el-menu-item
            :index="item.path + ''"
            v-for="item in menulist"
            :key="item.id"
            @click="saveNavState('/' + item.path)"
          >
            <template slot="title">
              <span>{{ item.authName }}</span>
            </template>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-main>
          <router-view></router-view>
        </el-main>
        <el-footer>版权归中科院所属</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      menulist: [
        {
          id: 0,
          authName: "文件格式",
          path: "format",
        },
        {
          id: 1,
          authName: "选择文件",
          path: "select",
        },
        {
          id: 2,
          authName: "标注图像",
          path: "mark",
        },
      ],
      isCollapse: false,
      activePath: "",
    };
  },
  created() {
    this.activePath = window.sessionStorage.getItem("activePath");
  },
  methods: {
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
    saveNavState(activePath) {
      window.sessionStorage.setItem("activePath", activePath);
      this.activePath = activePath;
    },
  },
};
</script>

<style>
.home-container {
  height: 100%;
}
.el-header {
  background-color: #eff2f5;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: rgb(108, 108, 112);
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #c7e3f3;
  .el-menu {
    border-right: none;
  }
}
.el-main {
  background-color: white;
}
.iconfont {
  margin-right: 10px;
}
.toggle-button {
  background-color: #c7e3f3;
  font-size: 10px;
  line-height: 24px;
  color: rgb(108, 108, 112);
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
.el-footer {
  background-color: #eff2f5;
}
</style>