<template>
  <div>
    <div id="map"></div>
  </div>
</template>

<script>
import AILabel from "ailabel";

export default {
  mounted() {
    const gMap = new AILabel.Map("map", {
      center: { x: 0, y: 0 },
      zoom: 800, //初始缩放级别
      mode: "PAN", // 绘制线段
    });
    const gFirstImageLayer = new AILabel.Layer.Image(
      "first-layer-image", // id
      {
        src: "https://img2.baidu.com/it/u=2053804264,1341330775&fm=26&fmt=auto&gp=0.jpg",
        width: 500, // 图片宽度
        height: 354, // 图片高度
        crossOrigin: false, // 图片是否跨域
        position: {
          // 图片左上角对应的坐标位置
          x: 0,
          y: 0,
        },
        grid: {
          // 3 * 3
          columns: [{ color: "#9370DB" }, { color: "#FF6347" }],
          rows: [{ color: "#9370DB" }, { color: "#FF6347" }],
        },
      }, // imageInfo
      { name: "第一个图片图层" }, // props
      { zIndex: 5 } // style
    );
    gMap.addLayer(gFirstImageLayer);
    const gFirstFeatureLayer = new AILabel.Layer.Feature(
      "first-layer-feature", // id
      { name: "第一个矢量图层" }, // props
      { zIndex: 10 } // style
    );
    gMap.addLayer(gFirstFeatureLayer);
    const gFirstFeaturePolygon = new AILabel.Feature.Polygon(
      "first-feature-polygon", // id
      {
        points: [
          { x: 0, y: 0 },
          { x: 100, y: 100 },
          { x: 100, y: 200 },
        ],
      }, // shape
      { name: "第一个多边形" }, // props
      { strokeStyle: "#FFD500", lineWidth: 1 } // style
    );
    gFirstFeatureLayer.addFeature(gFirstFeaturePolygon);
    const gFirstTextLayer = new AILabel.Layer.Text(
      "first-layer-text", // id
      { name: "第一个文本图层" }, // props
      { zIndex: 12} // style
    );
    gMap.addLayer(gFirstTextLayer);
    const text = new AILabel.Text("hkhlhnlhjkbkjbnkn");
    gFirstTextLayer.addText(text);
  },
};
</script>

<style>
#map {
  width: 500px;

  height: 500px;

  border: 1px solid rgb(17, 238, 65);
}
</style>